.about-company {
  span {
    color: @primary-color;
  }

  ul {
    background-color: @background-light;
    padding: 20px;
    border-radius: 16px;
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 20px;
  }
}

.about-company__item {
  display: flex;
  flex-direction: row;
  padding: 20px;
  border-radius: 10px;
  background-color: #ffffff;

  h3 {
    font-weight: bold;
    margin-bottom: 10px;
  }

  p {
    font-size: 18px;
    line-height: 20px;
    margin-bottom: 0;
  }

  span {
    display: block;
    min-width: 80px;
    height: 80px;
    margin-right: 20px;
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
  }

  &--happy {
    span {
      background-image: url("../../img/about-company/happy-users.svg");
    }
  }

  &--projects {
    span {
      background-image: url("../../img/about-company/projects.svg");
    }
  }

  &--team {
    span {
      background-image: url("../../img/about-company/team.svg");
    }
  }

  &--satisfaction {
    span {
      background-image: url("../../img/about-company/satisfaction.svg");
    }
  }
}
