.main-header {
  position: fixed;
  background-color: #ffffff;

  a {
    transition: all 0.6s ease-in-out;
  }

  a:hover {
    opacity: 0.6;
  }

  .navbar {
    padding: 20px 0;
    background: none;
  }

  .navbar-nav {
    margin: 0 auto;
  }

  .main-header__sign-in {
    border: none;
    font-weight: bold;
    margin-right: 10px;
  }

  .main-header__login {
    border: none;
    border-radius: 6px;
    font-weight: bold;
    color: #ffffff;
    background-color: @primary-color;
    padding: 10px 20px;
    transition: all 0.6s ease-in-out;

    &:hover {
      color: @primary-color;
      background-color: #ffffff;
    }
  }
}

