.service {
  ul {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;

    @media (max-width: 767px) {
      grid-template-columns: 1fr;
    }
  }

  li {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
    padding: 48px;
    border: 1px solid rgba(11, 22, 63, 0.12);
    border-top: none;
    transition: all 0.6s ease-in-out;

    &:hover {
      box-shadow: 0 10px 30px #0000001a;
    }

    h3 {
      font-weight: bold;
    }

    p {
      font-size: 16px;
    }

    @media (min-width: 1440px) {
      &:first-child,
      &:nth-child(4) {
        border-left: none;
      }

      &:nth-child(3n) {
        border-right: none;
      }

      &:last-child,
      &:nth-last-child(2),
      &:nth-last-child(3) {
        border-bottom: none;
      }
    }

    @media (max-width: 767px) {
      border: 1px solid rgba(11, 22, 63, 0.12);
    }
  }
}
