.intro {
  padding-bottom: 120px;

  .intro-content {
    width: 100%;
    height: 400px;
    background-color: rgba(201, 156, 156, 0.3);
  }

  h1 {
    font-weight: bold;
    font-size: 40px;
  }

  p {
    font-size: 18px;
  }

  span {
    color: #175cff;
  }
}

.intro__form {
  input {
    min-width: 280px;
    padding: 0.65rem 1rem;
    margin-bottom: 10px;
  }

  button {
    padding: 10px 16px;
  }

  span {
    max-width: 296px;
    position: relative;
    margin-left: 20px;
    margin-top: 0;
    margin-bottom: 5px;

    &::before {
      content: "";
      position: absolute;
      top: 5.5px;
      left: -11px;
      width: 15px;
      height: 15px;
      background-image: url("../img/control-mark.svg");
      background-repeat: no-repeat;
      background-size: contain;
    }
  }
}

.intro__customer-logos {
  display: grid;
  grid-auto-columns: 1fr;
  grid-row-gap: 16px;
  margin-right: auto;
  margin-left: auto;
  grid-column-gap: 40px;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
  grid-template-rows: auto;

  li {
    display: flex;
    justify-content: center;
    align-items: center;
  }

  img {
    max-height: 109px;
    transition: all 1s ease-in-out;

    &:hover {
      transform: scale(1.2);
    }
  }

  @media (max-width: 767px) {
    grid-template-columns: 1fr;

    li {
      margin-bottom: 10px;
    }
  }
}


.intro ul {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  column-gap: 10px;

  li {
    padding: 20px;
    border-radius: 16px;
    border: 2px solid @primary-color;
    background-color: @background-light;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .intro__advantages {
    font-weight: bold;
    font-size: 36px;
    color: @primary-color;
    margin-right: 15px;

    span {
      font-size: 18px;
    }
  }

  p {
    font-size: 14px;
    margin-bottom: 0;
  }
}
